.c-overlay {
  display: none; }
  .c-overlay ul {
    list-style-type: circle;
    list-style-position: inside;
    line-height: 32px; }
  .c-overlay--is-open {
    display: block;
    position: fixed;
    background: rgba(0, 0, 0, 0.75);
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    padding: 80px 5%;
    z-index: 1000; }
    .c-overlay--is-open .c-overlay__outer {
      background: #000;
      height: 100%;
      padding: 40px 20px 40px 40px;
      box-sizing: border-box;
      position: relative; }
    .c-overlay--is-open .c-overlay__inner {
      margin: 20px 0 40px;
      overflow-y: scroll;
      height: 100%; }
      @media (min-width: 1024px) {
        .c-overlay--is-open .c-overlay__inner {
          justify-content: space-between;
          display: flex; } }
    .c-overlay--is-open .c-overlay__inner::-webkit-scrollbar {
      margin: padding;
      width: 10px; }
    .c-overlay--is-open .c-overlay__inner::-webkit-scrollbar-track {
      -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3); }
    .c-overlay--is-open .c-overlay__inner::-webkit-scrollbar-thumb {
      background-color: #66a574;
      outline: 1px solid #66a574;
      border-radius: 6px;
      cursor: pointer; }
    .c-overlay--is-open .c-overlay__details {
      width: 100%; }
      @media (min-width: 1024px) {
        .c-overlay--is-open .c-overlay__details {
          padding-right: 40px;
          width: 40%; } }
    .c-overlay--is-open .c-overlay__image-container {
      padding: 0;
      margin-bottom: 40px;
      width: 100%; }
      @media (min-width: 1024px) {
        .c-overlay--is-open .c-overlay__image-container {
          margin-bottom: 0;
          padding: 20px;
          width: 50%; } }
    .c-overlay--is-open .c-overlay__image {
      width: 100%; }
      @media (min-width: 1024px) {
        .c-overlay--is-open .c-overlay__image {
          width: 100%; } }
    .c-overlay--is-open iframe {
      height: 100%;
      width: 100%;
      margin: 0 40px; }
    .c-overlay--is-open .c-overlay__heading {
      font-size: 36px; }
    .c-overlay--is-open .c-overlay__content {
      letter-spacing: 0.5px; }
    .c-overlay--is-open .c-overlay__close-button {
      margin: 20px;
      position: absolute;
      top: 0;
      right: 0; }
      .c-overlay--is-open .c-overlay__close-button span {
        position: absolute;
        left: -10000px;
        visibility: hidden; }

/* Close button */
.close {
  background: #000;
  position: absolute;
  right: 32px;
  top: 32px;
  width: 32px;
  height: 32px;
  opacity: 0.3;
  cursor: pointer;
  border: none;
  color: #fff; }

.close:hover {
  opacity: 1; }

.close:before, .close:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: #fff;
  top: 0; }

.close:before {
  transform: rotate(45deg); }

.close:after {
  transform: rotate(-45deg); }

/* Close button -- end */
